.cheaper {
    background-color: #000000;
    color: $white-text;
    padding-top: 36px;
    padding-bottom: 36px;

    @include md-block() {
        padding-bottom: 150px;
        padding-top: 270px; }

    &__container {
        @include wrapper(); }

    &__title {
        display: none; }

    &__title-image {}

    &__offer {
        padding-left: 6px;
        padding-right: 6px;
        text-align: center;
        max-width: 900px;
        margin-left: auto;
        margin-right: auto;

        @include md-block() {
            text-align: left;
            padding-top: 18px;
            padding-bottom: 28px;
            padding-left: 28px;
            padding-right: 28px;
            border-width: 15px;
            background-image: repeating-linear-gradient(-48deg, $white-text, $white-text 15px, transparent 15px, transparent 30px, $white-text 30px), repeating-linear-gradient(42deg, $white-text, $white-text 15px, transparent 15px, transparent 30px, $white-text 30px), repeating-linear-gradient(132deg, $white-text, $white-text 15px, transparent 15px, transparent 30px, $white-text 30px), repeating-linear-gradient(222deg, $white-text, $white-text 15px, transparent 15px, transparent 30px, $white-text 30px);
            background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
            background-position: 0 0, 0 0, 100% 0, 0 100%;
            background-repeat: no-repeat; } }


    &__offer-text {

        p {
            margin: 0; } }

    &__offer-price {
        margin-top: 12px;

        @include md-block() {
            margin-top: 30px; }

        p {
            font-size: 17px;
            line-height: 27px;
            text-transform: uppercase;
            margin-top: 0;
            margin-bottom: 0;

            @include md-block() {
                font-family: $black;
                font-size: 24px;
                line-height: 36px; } }

        b {
            @include md-block() {
                font-family: $black; } } } }
