.review {
    background-image: repeating-linear-gradient(-48deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(132deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px);
    background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
    background-position: 0 0, 0 0, 100% 0, 0 100%;
    background-repeat: no-repeat;

    &__user-blockquote {
        font-size: 20px;
        line-height: 30px;
        margin: 0;
        padding-top: 36px;
        padding-left: 18px;
        padding-right: 18px;
        padding-bottom: 12px;
        text-align: center;
        background: url('../img/review/quote.svg') no-repeat;
        background-size: 140px 108px;
        background-position: left 15px top 10px;
        word-wrap: break-word;

        @include md-block() {
            background-size: 208px 160px;
            background-position: left 42px top 35px;
            padding-top: 58px;
            padding-bottom: 32px; }

        blockquote {
            margin: 0;

            @include md-block() {
                max-width: 746px;
                margin-left: auto;
                margin-right: auto; } } }

    &__user-logo {
        margin-top: 32px;

        img {
            display: block;
            width: 158px;
            height: auto;
            margin-left: auto;
            margin-right: auto; } } }
