.header {
    background-color: $black-bg;
    color: $white-text;
    padding-bottom: 44px;
    background: url('../img/header/light-mobile.webp') no-repeat, $black-bg;
    background-position: center top -90px;
    background-size: auto;


    @include md-block() {
        padding-bottom: 60px;
        background: url('../img/header/light-desktop.png') no-repeat, $black-bg;
        background-position: right -240px center;
        background-size: auto; }

    @include l-block {
        background-position: right center; }

    &__container {
        @include wrapper();
        position: relative; }

    &__top {
        padding-top: 18px;

        @include md-block() {
            display: flex;
            align-items: baseline;
            padding-top: 24px;
            justify-content: space-between;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2); } }

    &__logo {
        width: 92px;
        height: 38px;

        img {
            width: 100%;
            max-width: 100%;
            height: auto; }

        @include md-block() {
            width: 112px;
            height: 46px;
            padding-bottom: 20px; } }

    &__menu-list {
        display: none;
        list-style: none;
        padding: 0;
        margin: 0;

        @include md-block() {
            display: flex; } }

    &__nav-list {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex; }

    &__nav-item {
        display: flex; }


    &__nav-link {
        color: $white-text;
        text-decoration: none;
        padding-bottom: 14px;
        padding-top: 14px;
        padding-left: 12px;
        padding-right: 12px;
        font-family: $regular;
        text-transform: uppercase; }

    &__menu-item {

        &--dropdown {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            display: none;

            @include md-block() {
                display: flex; }

            .header__nav-link {
                padding-right: 12px;
                background: url('../img/decor/triangle.svg') no-repeat;
                background-position: center right;
                background-size: 8px 4px; }

            &:focus {

                .header__menu-item--lvl2 {
                    display: block; } }

            &:hover {

                .header__menu-item--lvl2 {
                    display: block; } } }


        &--lvl2 {
            position: absolute;
            content: '';
            margin: 0;
            padding: 0;
            top: 100%;
            right: 0;
            min-width: 40px;
            background-color: $white-text;
            display: none;


            a {
                color: $black-text;
                text-decoration: none;
                padding-left: 8px;
                padding-right: 8px;
                padding-top: 4px;
                padding-bottom: 4px;

                &:hover {
                    color: $purple-text; } } } }

    &__info {
        padding-top: 40px;

        @include md-block() {
            display: grid;
            grid-template-areas: "a b" "c b";
            grid-template-columns: 54% calc(46% - 130px);
            column-gap: 130px;
            padding-top: 76px; } }

    &__message {
        text-align: center;
        padding-left: 18px;
        padding-right: 18px;

        @include md-block() {
            grid-area: a;
            padding-left: 0;
            padding-right: 0;
            text-align: left; } }

    &__message-caption {
        text-align: center;
        margin-top: 0;
        margin-bottom: 6px;
        font-size: 16px;
        line-height: 24px;

        @include md-block() {
            text-align: left;
            font-size: 24px;
            line-height: 36px;
            margin-bottom: 0; }

        b {
            display: block;

            @include md-block() {
                display: inline; } } }


    &__message-number {
        font-size: 47px;
        line-height: 56px;
        letter-spacing: 0.03em;
        font-family: $ultrabold;
        margin-top: 0;
        margin-bottom: 0;

        @include md-block() {
            font-size: 116px;
            line-height: 122px; }

        &--subtitle {
            font-size: 10px;
            line-height: 14px;
            letter-spacing: .58em;
            font-family: $regular;
            color: $green-text;
            display: block;
            text-transform: uppercase;
            margin-top: 4px;

            @include md-block() {
                font-size: 24px;
                line-height: 36px;
                margin-top: 0; } } }

    &__image {

        @include md-block() {
            grid-area: b;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative; }


        img {
            display: block;
            margin-left: auto;
            margin-right: auto;

            @include md-block() {
                width: 100%;
                height: 100%;
                object-fit: contain; }

            @include l-block() {
                left: 0;
                position: absolute;
                top: 0;
                bottom: 0;
                // width: auto
                // max-width: 100%
                width: auto;
                height: 100%; } } }

    &__description {
        padding-left: 18px;
        padding-right: 18px;
        margin-top: 100px;

        @include md-block() {
            margin-top: 96px;
            padding-left: 0;
            padding-right: 0;
            grid-area: c; }

        b {
            font-family: $ultrabold;
            color: $green-text; }

        p {
            font-size: 16px;
            line-height: 24px;
            margin-top: 0;
            margin-bottom: 0; } }

    &__description-footer {
        margin-top: 26px;

        @include md-block() {
            margin-top: 20px;
            display: flex;
            column-gap: 20px;
            align-items: flex-end; } }

    &__description-social {
        display: none;

        @include md-block() {
            display: block; }

        .social-links__link {
            @include md-block() {
                width: 62px;
                height: 62px;
                background-color: #525252; }

            svg {
                width: 32px;
                height: 32px;
                fill: #111111; } } }


    &__social-mobile {
        margin-top: 36px;
        display: flex;
        justify-content: center;

        @include md-block() {
            display: none; } }


    &__scroll {
        display: none;
        width: 134px;
        height: 132px;
        position: absolute;
        content: '';
        bottom: -135px;
        right: 20px;

        &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 58px;
            left: 0;
            top: 0;
            right: 0;
            background-color: $green-text;
            display: none; }

        @include md-block() {
            display: block; } } }
