.case {

    &__info {
        text-align: center;

        @include md-block() {
            width: 27%;
            text-align: left; }

        h4 {
            font-size: 20px;
            line-height: 30px;
            font-family: $ultrabold;
            margin: 0;
            text-transform: uppercase; }

        p {
            font-size: 18px;
            line-height: 27px;
            margin: 0;
            text-transform: uppercase; }

        h4 + p {
            margin-top: 30px; }

        p + p {
            margin-top: 27px; }

        b {
            font-family: $ultrabold; } }

    &__content {
        @include md-block() {
            display: flex;
            column-gap: 58px; } }


    &__image {
        margin-top: 26px;

        @include md-block() {
            width: 73%;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            margin-top: 0; }

        img {
            display: block;
            margin-left: auto;
            margin-right: auto;

            @include md-block() {
                width: 100%;
                height: 100%;
                object-fit: contain; } } }


    &__header {
        font-size: 26px;
        line-height: 40px;
        color: #060606;
        text-transform: uppercase;
        font-family: $ultrabold;
        text-align: center;
        margin-bottom: 25px;
        margin-top: 0;

        @include md-block() {
            text-align: left; }

        span {
            background-color: $green-text;
            display: inline-block;
            padding-top: 4px;
            padding-bottom: 4px;
            padding-left: 8px;
            padding-right: 8px;
            line-height: 40px; } } }
