.roadmap {
    padding-top: 24px;
    padding-bottom: 70px;

    &__container {
        padding-left: 36px;
        padding-right: 36px;
        max-width: 1200px;
        margin-left: auto;
        margin-right: auto; }

    &__item {
        position: relative;

        @include md-block() {
            padding-left: 34px;
            padding-bottom: 40px; }

        &--01 {
            margin-bottom: 62px;

            @include md-block() {
                max-width: 383px;
                padding-left: 84px;
                margin-bottom: 0; }

            &::before {
                position: absolute;
                content: '';
                width: 187px;
                height: 99px;
                background: url('../img/decor/arrow-01.svg') no-repeat center;
                top: calc(100% + 4px);
                left: 24px;
                pointer-events: none;

                @include md-block() {
                    left: unset;
                    right: -100px;
                    width: 225px;
                    height: 110px;
                    top: 8px; } }



            &::after {
                position: absolute;
                content: '';
                width: 45px;
                height: 45px;
                background: url('../img/decor/phone.svg') no-repeat center;
                right: 42px;
                top: -4px;

                @include md-block() {
                    height: 1px;
                    background: url('../img/decor/line.svg') repeat center;
                    background-size: 50px auto;
                    width: 200vw;
                    top: unset;
                    bottom: 0;
                    right: 82px; } } }


        &--02 {
            margin-bottom: 115px;

            @include md-block() {
                max-width: 383px;
                margin-left: 32%;
                padding-top: 8px;
                margin-bottom: 0;
                border-width: 15px;
                background-image: repeating-linear-gradient(-56deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, transparent 15px, transparent 15px, transparent 15px, transparent 30px, transparent 15px), repeating-linear-gradient(132deg, transparent 15px, transparent 15px, transparent 15px, transparent 30px, transparent 15px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px);
                background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
                background-position: 0 0, 0 0, 100% 0, 0 100%;
                background-repeat: no-repeat; }

            &::before {
                position: absolute;
                content: '';
                width: 191px;
                height: 94px;
                background: url('../img/decor/arrow-02.svg') no-repeat center;
                top: 100%;
                left: 14px;

                @include md-block() {
                    display: none; } }

            .roadmap__item-number {
                @include md-block() {
                    left: -40px; } } }

        &--03 {
            margin-bottom: 82px;

            @include md-block() {
                max-width: 383px;
                margin-left: 62%;
                padding-top: 8px;
                margin-bottom: 0;
                background-image: repeating-linear-gradient(-56deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, transparent 15px, transparent 15px, transparent 15px, transparent 30px, transparent 15px), repeating-linear-gradient(132deg, transparent 15px, transparent 15px, transparent 15px, transparent 30px, transparent 15px), repeating-linear-gradient(222deg, transparent 30px, transparent 30px, transparent 15px, transparent 30px, transparent 30px);
                background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
                background-position: 0 0, 0 0, 100% 0, 0 100%;
                background-repeat: no-repeat;
                background-color: #ffffff;
                margin-top: -1px; }

            &::before {
                position: absolute;
                content: '';
                width: 190px;
                height: 94px;
                background: url('../img/decor/arrow-03.svg') no-repeat center;
                background-size: contain;
                top: calc(100% + 17px);
                left: 24px;

                @include md-block() {
                    width: 230px;
                    height: 92px;
                    left: unset;
                    right: calc(100% + 81px);
                    top: calc(100% - 105px);
                    background: url('../img/decor/arrow-02.svg') no-repeat center; } }

            &::after {
                @include md-block() {
                    position: absolute;
                    content: '';
                    height: 1px;
                    background: url('../img/decor/line.svg') repeat center;
                    background-size: 40px auto;
                    width: 200vw;
                    top: unset;
                    bottom: 0;
                    left: 0; } }



            .roadmap__item-number {
                @include md-block() {
                    left: -40px; } } }



        &--04 {
            margin-bottom: 115px;


            @include md-block() {
                max-width: 412px;
                padding-top: 34px;
                padding-left: 84px;
                margin-bottom: 0; }

            &::before {
                position: absolute;
                content: '';
                width: 190px;
                height: 94px;
                background: url('../img/decor/arrow-04.svg') no-repeat center;
                top: calc(100% + 3px);
                left: 18px;

                @include md-block() {
                    display: none; } }

            &::after {
                @include md-block() {
                    position: absolute;
                    content: '';
                    height: 1px;
                    background: url('../img/decor/line.svg') repeat center;
                    background-size: 50px auto;
                    width: 200vw;
                    top: unset;
                    bottom: 0;
                    right: 87px; } } }

        &--05 {

            @include md-block() {
                max-width: 483px;
                margin-left: 34%;
                padding-top: 8px;
                background-image: repeating-linear-gradient(-56deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, transparent 15px, transparent 15px, transparent 15px, transparent 30px, transparent 15px), repeating-linear-gradient(132deg, transparent 15px, transparent 15px, transparent 15px, transparent 30px, transparent 15px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px);
                background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
                background-position: 0 0, 0 0, 100% 0, 0 100%;
                background-repeat: no-repeat; }

            &::before {
                // position: absolute
                display: block;
                content: '';
                width: 71px;
                height: 94px;
                background: url('../img/decor/flag.svg') no-repeat center;
                margin-right: -10px;
                float: right;

                @include md-block() {
                    position: absolute;
                    bottom: 4px;
                    left: calc(100% + 3px); } }





            .roadmap__item-number {
                @include md-block() {
                    left: -40px; } } } }


    &__item-number {
        font-size: 20px;
        line-height: 30px;
        color: $black-text;
        font-family: $bold;
        margin-bottom: 12px;

        @include md-block() {
            position: absolute;
            bottom: -36px;
            margin-bottom: 0;
            left: 0; } }

    &__item-text {
        margin-top: 0;
        margin-bottom: 0;
        font-size: 16px;
        line-height: 24px;

        @include md-block() {
            font-size: 20px;
            line-height: 30px; }

        &--splash {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                width: 36px;
                height: 32px;
                background: url('../img/decor/splash.svg') no-repeat center;
                top: -26px;
                left: calc(100% - 4px); } }

        &--splash-left {
            position: relative;

            &::before {
                display: none;
                content: '';
                position: absolute;
                width: 36px;
                height: 32px;
                background: url('../img/decor/splash-left.svg') no-repeat center;
                top: -26px;
                right: calc(100% - 4px);

                @include md-block() {
                    display: block; } } } }



    &__footer {
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 15px;
        padding-right: 15px;
        margin-bottom: 0;
        margin-top: 45px;
        margin-left: -15px;
        margin-right: -15px;
        font-size: 16px;
        line-height: 24px;
        border-width: 15px;
        background-image: repeating-linear-gradient(-48deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(132deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px);
        background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
        background-position: 0 0, 0 0, 100% 0, 0 100%;
        background-repeat: no-repeat;

        font-size: 16px;
        line-height: 24px;

        @include md-block() {
            font-size: 20px;
            line-height: 30px;
            padding-left: 28px;
            padding-right: 28px;
            padding-top: 18px;
            padding-bottom: 18px;
            margin-top: 105px; } } }
