.statistics {
    padding-top: 24px;
    padding-bottom: 42px;
    &__container {
        @include wrapper(); }

    &__title {
        font-size: 27px;
        line-height: 40px;
        font-family: $bold;
        text-transform: uppercase;
        margin-top: 0;
        margin-bottom: 30px; }

    &__content {
        display: grid;
        row-gap: 16px;

        // +md-block()
        //     grid-template-columns: 25% 25%
        //     column-count: 2
        //     column-gap: 34px
 }        //     row-gap: 34px

    &__item {
        max-width: 382px;
        width: 100%;

        @include md-block() {
            margin-bottom: 32px; }


        img {
            width: 100%;
            height: auto;
            display: block; } } }
