.accordeon {
    margin: 0;
    padding: 0;
    list-style: none;

    @include md-block() {
        padding-top: 12px;
        padding-bottom: 12px;
        padding-left: 40px;
        padding-right: 58px;
        border-width: 15px;
        background-image: repeating-linear-gradient(-48deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px), repeating-linear-gradient(42deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px), repeating-linear-gradient(132deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px), repeating-linear-gradient(222deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px);
        background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
        background-position: 0 0, 0 0, 100% 0, 0 100%;
        background-repeat: no-repeat; }


    &__item {
        border-top: 1px solid $green-text;

        &:last-child {
            border-bottom: 1px solid $green-text;

            @include md-block() {
                border-bottom: none; } }

        &:first-child {
            @include md-block() {
                border-top: none; } } }



    &__header {
        display: flex;
        background-color: transparent;
        padding-left: 0;
        padding-right: 0;
        border: none;
        width: 100%;
        padding-right: 12px;
        padding-top: 9px;
        padding-bottom: 13px;
        cursor: pointer;

        &:hover {
            background-color: $green-text;

            .accordeon__header-text {
                color: #000000; }

            .accordeon__header-number {
                color: #000000; }

            .accordeon__header-icon {
                background-color:#000000 {} } }

        @include md-block() {
            padding-top: 16px;
            padding-bottom: 16px; } }


    &__header-text {
        font-size: 13px;
        line-height: 20px;
        font-family: $semibold;
        color: $white-text;
        text-transform: uppercase;
        text-align: left;

        @include md-block() {
            font-size: 22px;
            line-height: 32px; } }

    &__header-number {
        font-size: 13px;
        line-height: 20px;
        font-family: $semibold;
        color: $green-text;
        padding-right: 12px;

        @include md-block() {
            font-size: 22px;
            line-height: 32px;
            padding-right: 60px; } }


    &__header-icon {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        background-color: $green-text;
        align-self: center;

        @include md-block() {
            width: 32px;
            height: 32px; } }


    &__item-content {
        display: none; }

    &--light {
        @include md-block() {
            background-image: repeating-linear-gradient(-48deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(132deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px); }


        .accordeon__item {
            color: $black-text;
            border-color: $black-text; }



        .accordeon__header {

            &:hover {
                background-color: $purple-text;

                .accordeon__header-number {
                    color: $white-text; }

                .accordeon__header-text {
                    color: $white-text; }

                .accordeon__header-icon {
                    background-color: $white-text; } } }

        .accordeon__header-number {
            color: $gray-text; }

        .accordeon__header-text {
            color: $black-text; }

        .accordeon__header-icon {
            background-color: $black-text; } } }


