@mixin font-face($font-name, $path) {
  @font-face {
    font-family: quote($font-name);
    src: url(#{$path}.woff2) format("woff2");
    font-weight: normal;
    font-style: normal;
    font-display: swap; } }

@include font-face('RFDewiExpanded-Black', '../fonts/RFDewiExpanded-Black');
@include font-face('RFDewiExpanded-Bold', '../fonts/RFDewiExpanded-Bold');
@include font-face('RFDewiExpanded-Semibold', '../fonts/RFDewiExpanded-Semibold');
@include font-face('RFDewiExpanded-Ultrabold', '../fonts/RFDewiExpanded-Ultrabold');
@include font-face('RFDewiExpanded-Regular', '../fonts/RFDewiExpanded-Regular');
