.ticker {
    width: 110%;
    padding-top: 6px;
    padding-bottom: 6px;
    background-color: $green-text;
    position: relative;
    box-sizing: border-box;

    @include md-block() {
        padding-top: 10px;
        padding-bottom: 10px; }




    &__inner {
        display: flex;
        overflow-x: hidden; }

    &__inner-item {
        margin-top: 0;
        margin-bottom: 0;
        white-space: nowrap;
        padding-left: 8px;
        padding-right: 8px;
        font-family: $ultrabold;
        font-size: 12px;
        line-height: 18px;
        color: $black-text;
        animation: ticker-slide 10s linear infinite;

        @include md-block() {
            font-size: 26px;
            line-height: 40px; } }

    &--purple {
        background-color: $purple-text;
        color: #ffffff;

        .ticker__inner-item {
            color: #ffffff; } } }


@include ticker-slide();
