.table {
    width: 100%;

    &__tr {
        padding-top: 6px;
        padding-bottom: 6px;
        display: block;

        @include md-block() {
            display: flex;
            column-gap: 16px;
            padding: 0; } }

    &__th {
        font-size: 26px;
        line-height: 39px;
        color: $purple-text;
        text-transform: uppercase;
        padding-bottom: 15px;
        text-align: left;
        font-family: $bold;

        @include md-block() {
            text-align: center;
            color: #ffffff;
            background-color: $purple-text;
            font-size: 20px;
            line-height: 33px;
            padding-top: 2px;
            padding-bottom: 2px; } }


    &__td {
        display: block;
        font-size: 16px;
        line-height: 21px;
        padding: 0;

        @include md-block() {
            display: table-cell;
            padding-top: 6px;
            padding-bottom: 6px;
            padding-left: 12px;
            padding-right: 12px;
            font-size: 18px;
            line-height: 27px;
            flex-grow: 1; } }



    &__name {
        font-family: $semibold;

        @include md-block() {
            font-family: $regular;
            width: 68%; } }

    &__price {
        font-size: 10px;
        line-height: 15px;
        color:#9C9C9C {}
        margin-top: 3px;

        @include md-block() {
            font-size: 18px;
            line-height: 27px;
            font-family: $bold;
            color: $black-text;
            text-align: right;
            width: 32%;
            margin-top: 0;
            display: flex;
            align-items: center;
            flex-direction: row-reverse; } }


    &__price-item {
        font-family: $bold;

        @include md-block() {
            display: none; }

        &--small {

            @include md-block() {
                font-family: $semibold;
                font-size: 14px;
                line-height: 21px; } } }

    tbody .table__tr:nth-child(even) td {
        @include md-block() {
            background-color: #E5E5E5; } }

    small {
        font-size: 12px;
        line-height: 16px; } }



