.footer {

    &__container {
        @include wrapper(); }

    &__logo-content {
        overflow: hidden; }

    &__logo-list {
        display: flex;
        animation: ticker-slide 10s linear infinite; }



    &__logo-item {
        width: 130px;
        height: 130px;
        flex-shrink: 0;
        border-left: 1px solid #ADADAD;
        box-sizing: border-box;
        padding: 20px;
        border-top: 1px solid #ADADAD;
        border-bottom: 1px solid #ADADAD;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 106px;
            height: auto;

            @include md-block() {
                width: 254px; } }


        @include md-block() {
            width: 320px;
            height: 320px; }

        &:last-child {
            border-right: 1px solid #ADADAD; } }

    &__info {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-areas: "a b" "c b";
        padding-top: 28px;
        padding-bottom: 20px;


        @include md-block() {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-bottom: 42px;
            padding-top: 32px; } }

    &__copyrights {
        font-size: 10px;
        line-height: 15px;
        grid-area: c;

        @include md-block() {
            font-size: 16px;
            line-height: 24px;
            margin-top: 8px; } }

    &__social {
        grid-area: b;
        margin-left: auto;

        @include md-block() {
            margin-top: 10px; } }


    &__logo {
        grid-area: a;
        width: 70px;

        @include md-block() {
            margin-right: auto;
            width: 133px; }

        img {
            display: block;
            width: 100%;
            height: auto; } }


    .social-links {
        flex-direction: column;

        @include md-block() {
            flex-direction: row; } }

    .social-links__link {
        width: 40px;
        height: 40px;
        background-color: transparent;

        svg {
            width: 32px;
            height: 32px; } } }


@include ticker-slide();
