.cases {
    background-color: #060606;
    color: $white-text;
    padding-bottom: 42px;

    &__header {
        background-color: #ffffff;
        color: $black-text; }

    &__container {
        padding-left: 18px;
        padding-right: 18px;

        max-width: 1100px;
        margin-left: auto;
        margin-right: auto; }


    &__slider {
        padding-bottom: 90px;
        padding-top: 22px;

        @include md-block() {
            padding-bottom: 100px;
            padding-top: 66px; } }

    &__slider-next {
        width: 50px;
        height: 58px;
        background: url('../img/slider-cases/next-btn.svg') no-repeat center;
        background-size: contain;
        top: unset;
        bottom: 0;
        right: calc(50% - 35px);
        transform: translateX(50%);

        @include md-block() {
            right: 0;
            transform: none;
            width: 37px;
            height: 42px; }



        &::after {
            display: none; } }

    &__slider-prev {
        width: 50px;
        height: 58px;
        background: url('../img/slider-cases/prev-btn.svg') no-repeat center;
        background-size: contain;
        top: unset;
        bottom: 0;
        left: calc(50% - 35px);
        transform: translateX(-50%);

        @include md-block() {
            left: 0;
            transform: none;
            width: 37px;
            height: 42px; }

        &::after {
            display: none; } }

    &__pagination {
        display: none;

        @include md-block() {
            display: block; }

        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            background-color: #ffffff;
            opacity: 1;
            border-radius: 0;
            margin-left: 7px !important;
            margin-right: 7px !important; }

        .swiper-pagination-bullet-active {
            width: 15px;
            height: 15px;
            background-color: $green-text;
            border-radius: 0; } } }





