.reviews {
    background-color: $light-bg;
    padding-top: 64px;
    padding-bottom: 80px;

    @include md-block() {
        padding-top: 144px;
        padding-bottom: 144px; }

    &__container {
        max-width: 1188px;
        box-sizing: border-box;
        margin-left: auto;
        margin-right: auto;

        @include md-block() {
            padding-left: 0;
            padding-right: 0; } }


    &__slider {
        padding-left: 48px;
        padding-right: 24px;

        @include md-block() {
            padding-left: 90px;
            padding-right: 90px; } }



    &__slider-item {
        width: 214px;

        @include md-block() {
            width: auto; } }


    &__slider-next {

        @include md-block() {
            width: 48px;
            height: 60px;
            background: url('../img/slider-review/next-btn.svg') no-repeat center;
            background-size: contain; }

        &::after {
            display: none; } }

    &__slider-prev {

        @include md-block() {
            width: 48px;
            height: 60px;
            background: url('../img/slider-review/prev-btn.svg') no-repeat center;
            background-size: contain; }

        &::after {
            display: none; } } }




