.we-can {
    background-color: $black-bg;
    color: $white-text;
    padding-bottom: 20px;
    position: relative;

    @include md-block() {
        padding-top: 55px;
        padding-bottom: 120px; }

    &::after {
        position: absolute;
        content: '';
        left: 0;
        right: 0;
        width: 0;
        bottom: -40px;
        height: 0;
        border-bottom: 40px solid transparent;
        border-left: 0 solid transparent;
        border-right: 100vw solid $black-bg;
        z-index: 1;

        @include sm-block() {
            border-bottom: 70px solid transparent;
            bottom: -70px; }


        @include md-block() {
            bottom: -110px;
            border-bottom: 110px solid transparent; } }

    &__container {
        @include wrapper(); }


    &__title {
        font-size: 40px;
        line-height: 48px;
        color: $white-text;
        font-family: $regular;
        margin-top: 0;
        margin-bottom: 10px;

        @include md-block() {
            font-size: 64px;
            line-height: 76px;
            margin-bottom: 28px;
            max-width: 445px; }

        b {
            font-family: $bold;
            color: $green-text; } }

    &__title-icon {
        width: 32px;
        height: 32px;
        background-color: $green-text;
        display: inline-flex;
        border-radius: 6px;
        margin-right: 16px;

        @include md-block() {
            width: 52px;
            height: 52px;
            border-radius: 10px;
            margin-right: 24px; } }

    &__subtitle {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 95px;
        margin-top: 0;
        display: inline-block;
        max-width: 184px;

        @include md-block() {
            font-size: 36px;
            line-height: 42px;
            max-width: none; }

        b {
            font-family: $bold;
            color: $green-text; }

        &--youtube {
            position: relative;
            z-index: 1;

            @include md-block() {
                z-index: 10; }

            &::after {
                z-index: -1;
                position: absolute;
                right: -86px;
                top: -42px;
                content: '';
                width: 170px;
                height: 148px;
                background: url('../img/social/youtube-mobile.webp') no-repeat center;
                background-size: contain;
                pointer-events: none;

                @include md-block() {
                    width: 400px;
                    height: 336px;
                    background: url('../img/social/youtube.webp') no-repeat center;
                    background-size: contain;
                    right: -450px;
                    top: -172px; } } }


        &--tg {
            position: relative;
            z-index: 1;

            @include md-block() {
                z-index: 10; }

            &::after {
                position: absolute;
                z-index: -1;
                right: -150px;
                top: -20px;
                content: '';
                width: 116px;
                height: 125px;
                background: url('../img/social/tg-mobile.webp') no-repeat center;
                background-size: contain;
                pointer-events: none;

                @include md-block() {
                    background: url('../img/social/tg.webp') no-repeat center;
                    width: 400px;
                    height: 416px;
                    background-size: contain;
                    right: -470px;
                    top: -220px;
                    z-index: 2; } } } }


    &__ticker {
        position: relative;
        z-index: 2;
        transform: rotate(5deg) translate(-5%, 56px);
        width: 110%;

        @include sm-block() {
            transform: rotate(4deg) translate(-5%, 66px); }

        @include md-block() {
            transform: rotate(5deg) translate(-5%, 210px); } }



    &--light {
        background-color: $light-bg;
        color: $black-text;
        padding-top: 54px;

        @include md-block() {
            padding-top: 100px; }

        &::after {
            position: absolute;
            content: '';
            left: 0;
            right: 0;
            width: 0;
            bottom: -40px;
            height: 0;
            border-bottom: 40px solid transparent;
            border-left: 0 solid transparent;
            border-right: 100vw solid #fafafa;
            z-index: 1;

            @include sm-block() {
                border-bottom: 70px solid transparent;
                bottom: -70px; }



            @include md-block() {
                bottom: -110px;
                border-bottom: 110px solid transparent; } }


        .we-can__title {
            color: $black-text;

            b {
                color: $purple-text; } }

        .we-can__subtitle {

            b {
                color: $purple-text; } }

        .we-can__title-icon {
            background-color: $purple-text; } } }


